// src/pages/login.tsx
import { useState } from 'react'
import { signIn } from 'next-auth/react'
import Head from 'next/head'
import Image from 'next/image'
import Link from 'next/link'

export default function LoginPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState<string | null>(null)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    const res = await signIn('credentials', {
      redirect: false,
      email,
      password,
    })
    if (res?.error) {
      setError(res.error)
    }
  }

  return (
    <>
      <Head>
        <title>Iniciar Sesión • Bloom Fisio</title>
        <link rel="icon" href="/images/logo_bloom_clean.png" />
      </Head>
      <div className="vh-100 d-flex flex-column justify-content-center align-items-center">
        {/* Logo arriba a la izquierda */}
        <div className="position-absolute top-0 start-0 p-3">
          <Link href="/">
            <Image
              src="/images/logo_bloom_clean.png"
              alt="Bloom Fisio"
              width={80}
              height={80}
            />
          </Link>
        </div>

        {/* Formulario */}
        <form
          onSubmit={handleSubmit}
          className="contact-card p-4"
          style={{ width: '100%', maxWidth: '400px' }}
        >
          <h2 className="mb-3 text-center">Iniciar Sesión</h2>
          {error && <div className="alert alert-danger">{error}</div>}

          <div className="form-floating mb-3">
            <input
              id="email"
              name="email"
              type="email"
              className="form-control"
              placeholder="Correo electrónico"
              value={email}
              onChange={e => setEmail(e.target.value)}
              required
            />
            <label htmlFor="email">Correo electrónico</label>
          </div>

          <div className="form-floating mb-4">
            <input
              id="password"
              name="password"
              type="password"
              className="form-control"
              placeholder="Contraseña"
              value={password}
              onChange={e => setPassword(e.target.value)}
              required
            />
            <label htmlFor="password">Contraseña</label>
          </div>

          <button type="submit" className="btn btn-orange w-100 py-2">
            Entrar
          </button>

          <p className="text-center mt-3">
            ¿No tienes cuenta? <Link href="/register">Regístrate</Link>
          </p>
        </form>
      </div>
    </>
  )
}
